body {
  font-family: Roboto, sans-serif;
}

.loader, .error {
  text-align: center;
  padding: 20px;
  display: none;
}

.cat-info {
  width: 80%;
  max-width: 600px;
  text-align: center;
  background: #f9f9f9;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin: 20px auto;
  padding: 20px;
  display: none;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.cat-info img {
  width: 100%;
  max-width: 600px;
  height: auto;
  border-radius: 8px;
  margin-bottom: 20px;
}

.cat-info p {
  color: #333;
  margin: 10px 0;
  font-size: 1rem;
}

.cat-info p.name {
  font-size: 1.5rem;
  font-weight: bold;
}

